.project {
    padding: 1rem;
    text-align: center;

}

.project div {
    position: relative;
    display: inline-block;
    /* if you need ie6/7 support */
    *display: inline;
    zoom: 1;
}

.project p {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 0;
    bottom: -16px;
    height: 25%;
    width: 100%;
    color: #ffffff;
    background: #000;
    background: rgba(0, 0, 0, 0.64);
}

.project-image {
    display: block;
    max-width: 100%;
    height: auto;

}

.icon{
    justify-content: center;
    align-items: center;
    font-size: 30px;
}

